import Vue from "vue";
import VueRouter from "vue-router";
import importDataToDrupal from "../components/import-drupal/importDataToDrupal.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "import data",
    component: importDataToDrupal,
  },
  {
    path: "/delete-terms",
    name: "suppresion de terme",
    props: true,
    component: () => import("../components/DeleteTerms/DeleteTerms.vue"),
  },
  {
    path: "/*",
    redirect: "/",
  },
];

const router = new VueRouter({
  //mode: "history",
  //base: process.env.BASE_URL,
  mode: "hash",
  routes,
});

export default router;
